import cookieParser from 'set-cookie-parser'
import util from './util'

/**
 * Cookie ç±»
 */
class Cookie {
  /**
   * æé å½æ°
   */
  constructor (props) {
    this.name = props.name || ''
    this.value = props.value || ''
    // other
    this.domain = props.domain || ''
    this.path = props.path || '/'
    this.expires = props.expires ? new Date(props.expires) : null
    this.maxAge = props.maxAge ? parseInt(props.maxAge) : null
    this.httpOnly = !!props.httpOnly
    // è®°å½æ¶é´
    this.dateTime = props.dateTime ? new Date(props.dateTime) : new Date()
  }

  /**
   * è®¾ç½® cookie, å° set-cookie å­ç¬¦ä¸²è½¬æ¢ä¸º Cookie å¯¹è±¡
   */
  set (setCookieStr = '') {
    var cookie = cookieParser.parse(setCookieStr)[0]
    if (cookie) {
      Object.assign(this, cookie)
      // æ´æ°è®¾ç½®æ¶é´
      this.dateTime = new Date()
    }

    return this
  }

  /**
   * åå¹¶ cookie
   * @param  {Cookie} cookie cookie å¯¹è±¡
   * @return {Cookie}        this
   */
  merge (cookie) {
    return Object.assign(this, cookie)
  }

  /**
   * éªè¯ cookie æ¯å¦è¿ææ
   * @return {Boolean} æ¯å¦ææ
   */
  isExpired () {
    // maxAge ä¸º 0ï¼æ æ
    if (this.maxAge === 0) {
      return true
    }
    // å­æ´»ç§æ°è¶åº maxAgeï¼æ æ
    if (this.maxAge > 0) {
      let seconds = (Date.now() - this.dateTime.getTime()) / 1000
      return seconds > this.maxAge
    }
    // expires å°äºå½åæ¶é´ï¼æ æ
    if (this.expires && this.expires < new Date()) {
      return true
    }
    return false
  }

  /**
   * éªè¯ cookie æ¯å¦å¯æä¹å
   * @return {Boolean} æ¯å¦å¯æä¹å
   */
  isPersistence () {
    return this.maxAge ? this.maxAge > 0 : true
  }

  /**
   * éªè¯ cookie æ¯å¦å¨æå®ç domain èå´å
   * @param  {String}  domain    åå
   * @return {Boolean}           æ¯å¦å¨æå®ç domain èå´å
   */
  isInDomain (domain) {
    let scopeDomains = util.getCookieScopeDomain(domain)
    return scopeDomains.indexOf(this.domain) >= 0
  }

  /**
   * éªè¯ cookie æ¯å¦å¨æå®ç path èå´å
   * @param  {String}  path    urlè·¯å¾
   * @return {Boolean}         æ¯å¦å¨æå®ç path èå´å
   */
  isInPath (path) {
    return path.indexOf(this.path) === 0 || this.path.replace(/\/$/, '') === path
  }

  /**
   * éåå¯¹è±¡ç toString æ¹æ³
   */
  toString () {
    return [this.name, this.value].join('=')
  }
}

export default Cookie
